"use client";
import { FiSearch, FiPlus, FiEdit, FiBox, FiTrash } from "react-icons/fi";

export default function Home() {
  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-transparent">
    

      <div className="flex flex-wrap justify-center gap-4">
        <ActionButton
          color="bg-black"
          icon={<FiSearch size={48} />}
          label="Cari Barang"
        />
        <ActionButton
          color="bg-gray-800"
          icon={<FiPlus size={48} />}
          label="Tambah Barang"
        />
        <ActionButton
          color="bg-gray-600"
          icon={<FiEdit size={48} />}
          label="Edit Barang"
        />
        <ActionButton
          color="bg-gray-400"
          icon={<FiBox size={48} />}
          label="Stok Barang"
        />
        <ActionButton
          color="bg-red-700"
          icon={<FiTrash size={48} />}
          label="Hapus Barang"
        />
      </div>
    </div>
  );
}

function ActionButton({
  icon,
  label,
  color,
}: {
  icon: React.ReactNode;
  label: string;
  color: string;
}) {
  return (
    <button
      className={`${color} text-white w-32 h-32 rounded-2xl flex flex-col items-center justify-center shadow-xl hover:scale-105 transition`}
    >
      {icon}
      <span className="mt-3 text-sm font-semibold text-center">{label}</span>
    </button>
  );
}
